*{
  box-sizing:border-box ;
  margin: 0;
  padding: 0;
}


.hi{
  font-size: 45px;
  font-weight: 600;
  text-shadow: 2px 2px 2px black;
}

header{
  text-align: center;
  padding: 4px 10%;
  background-color:#fb8014
}

.List_Api{
  width: 60%;
  display: flex;
  justify-content: space-between;
  border: 2px solid #333;
  margin-bottom:-2px;
  padding:10px 0 10px 0;
  background-color:#ffe0c5 ;
  padding-left: 15px;
  padding-right: 20px;
  
}

.List_Name{
  width: 60%;
  display: flex;
  justify-content: space-between;
  border: 2px solid #333;
  margin-bottom:-2px;
  padding:10px 0 10px 0;
  background-color:#fb8014 ;
}

.num{
  padding-left: 15px;
  color:white;
}

.date {
  padding-right: 100px;
  color:white;
}

.time{
  padding-right: 40px;
  color:white;
}
.status{
  color: white;
  padding-right: 20px;
}

.list_item{
  padding-left: 30px;
}

.list{
  padding:0 0 350px 0;
  background-color: #f5f5f5;
}

li{
  width: fit-content;
  margin: auto;
  font-size: 25px;
  background-color: white;
}
.search{
  margin-bottom: 20px;
  margin-left: 40%; 
  width: 20%;
  height: 30px;
  font-size: 20px;
}

.searchBox{
  border: 2px solid #000;
}

.subtitle{
  font-size: 20px;
  text-align: center; 
  padding:10px 0 10px 0;
  color: black;
}