@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);

body {
  background: rgb(255, 214, 152);
  font-family: 'Open Sans', sans-serif;
}

.login {
  padding-top: 100px;
  width: 400px;
  margin: 16px auto;
  font-size: 16px;
}

/* Reset top and bottom margins from certain elements */
.login-header,
.login p {
  margin-top: 0;
  margin-bottom: 0;
}

/* The triangle form is achieved by a CSS hack */
.login-triangle {
  width: 0;
  margin-right: auto;
  margin-left: auto;
  border: 12px solid transparent;
  border-bottom-color:#fb8014;
}

.login-header {
  background: #fb8014;
  padding: 20px;
  font-size: 1.4em;
  font-weight: normal;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
}

.login-container {
  background: #ffebd3;
  padding: 12px;
  height: 100px;
}

.login-container button {
  display:block;
  margin: 0 auto;
  margin-top: 20px;
  text-align: center;
  padding: 5px 20px;
  cursor: pointer;
  border: 1px solid #fb8014;
  border-radius: 30px;
  font-weight: 700;
  transition: all 0.6s;
}

.login-container button:hover {
  background-color: #fb8014;
  color: #fff;

}

/* Every row inside .login-container is defined with p tags */
.login p {
  padding: 12px;
}



